@import "../../../../utils/variables.scss";

.wrapper {
  background-color: white;
  max-width: 800px;
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  position: relative;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  .icon {
    border: 1px solid $themePurple;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    svg {
      color: $themePurple;
      animation: pulseAnim 1.5s linear infinite alternate;
    }
  }
  .title {
    font-weight: 600;
    font-size: 20px;
    margin-bottom: 30px;
    color: $themePurple;
    text-align: center;
  }
  .footer {
    display: flex;
    justify-content: flex-end;
    margin-top: 50px;
    button {
      width: 150px;
      height: 40px;
      background-color: $themePurple;
      color: white;
      border-radius: 5px;
      font-weight: 500;
    }
  }
}

@media screen and (max-width: 850px) {
  .wrapper {
    width: 94%;
  }
}

@keyframes pulseAnim {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.4);
  }
}
