.wrapper {
    display: flex;
    width: 100%;
    align-items: center;
    padding: 0px 25px;
    position: absolute;
    width: 100%;
    margin-top: auto;
    position: relative;
    height: 80px;
    background: #fff;
    justify-content: space-between;
    gap: 20px;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background: aliceblue;
    z-index: 100;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;


  div {
    color: #707070;
    font-size: 14px;
  }
  .instructionsBtn {
    cursor: pointer;
    font-size: 0.9rem;
    color: #707070;
    text-decoration: underline;
  }

  button.prevBtn {
    color: #707070;
    font-size: 14px;
  }
}

@media screen and (max-width: 900px) {
  .wrapper {
    padding: 0px 20px;
  }
}
