@import "../../../utils/variables.scss";

.wrapper {
  display: flex;
  flex-direction: column;
  font-family: "Outfit", sans-serif;
  width: 100%;
  padding: 40px;

  .question {
    font-size: 22px;
    color: $themePurple;
    margin-bottom: 20px;
    width: 100%;

    img {
      margin-top: 10px;
      max-width: 100%;
      max-height: 180px;
      height: 100%;
      object-fit: contain;
    }
  }

  .answer {
    font-size: 14px;
    display: flex;
    width: 100%;

    img {
      max-height: 100px;
      max-width: 200px;
    }
  }
}

@media screen and (max-width: 600px) {
  .wrapper {
    .question {
      img {
        width: 100%;
        height: auto !important;
      }
    }

    .answer {
      font-size: 14px;
      display: flex;
      width: 100%;

      img {
        height: 100px;
      }
    }
  }
}